@import './mixin';

* {
    margin: 0;
    padding: 0;
}

body {
    height: 100vh;
    width: 100%;
    display: flex;
    justify-content: center;
    align-items: center;
}

.tooltip {
    height: 20px;
    width: 100px;
    background: purple;
    color: #fff;
    border-radius: 7px;
    text-align: center;
    line-height: 20px;
    padding: 5px;

    position: relative;
    cursor: pointer;

    @include setTooltip("your text");

}